import React from 'react';
import {connect} from "react-redux";
import {Link} from "react-router-dom";
import styles from "./Cart.module.css";
import {checkoutCart} from "../../redux/actions/index";
import { Redirect } from 'react-router';
const Cart = (props) => {
    let total=0;
    let handleCheckout=()=>{
        let payload={
            amount:total
        }
        props.checkout(payload)
    }
    return (
        <>  
            {props.checkoutStatus?<Redirect to="/react/"/>:null}
            <h1>Cart</h1>
            <div className="card-deck d-flex flex-wrap products">
                {props.cart.length==0?<h1>CART EMPTY</h1>:props.cart.map((item, idx) => {
                    total+=item.price * item.count;
                return (
                    <div className="card product" style={{
                        width:"18rem"
                    }}>
                    <img className="card-img-top" src={item.preview} alt="Card image cap"/>
                    <div className="card-body">
                    <Link to={`/react/Products/${item.id}`}>
                        <h5 className="card-title">{item.name}</h5>
                        <p className="card-para card-text">Some quick example text to build on the card title and make up the bulk of the card's content. Some quick example text to build on the card title and make up the bulk of the card's content</p>
                        <h5>Price {item.price} X {item.count} = {item.price * item.count}</h5>
                    </Link>
                </div>
                </div>)})}
            </div>
            <h1>Total Amount = {total}</h1>
            <div className={styles.btn}>
                    <button onClick={()=>{handleCheckout()}}>Checkout</button>
            </div>
        </>
     );
}
const mapStateToProps=(store)=>({
    cart:store.productReducer.cart,
    checkoutStatus:store.productReducer.checkoutStatus
  }); 
  
  const mapDispatchToProps=(dispatch)=>({
    checkout:(payload)=>dispatch(checkoutCart(payload))
  })
  
  export default connect(mapStateToProps,mapDispatchToProps)(Cart);