import * as actiontypes from "../../actionTypes/index";
const initialProduct={
    products:[],
    cart:[],
    cart_count:0,
    checkoutStatus:false
};

const ProductReducer=(state=initialProduct,action)=>{
    switch(action.type){
        case actiontypes.GET_PRODUCTS:{
            return{
                ...state,
                products:action.payload
            }
        }
        case actiontypes.ADD_TO_CART:{
            let temp_cart=[...state.cart];
            let index=temp_cart.findIndex((item)=>item.id==action.payload.id);
            let temp_count=state.cart_count;
            if(index==-1){
                temp_cart.push({...action.payload,count:1});
                temp_count++;
            }
            else{
                temp_cart[index].count++;
                
            }
            
            return{
                ...state,
                cart:temp_cart,
                cart_count:temp_count,
                checkoutStatus:false
                
            }
        }
        case actiontypes.CHECKOUT_CART:{
            return{
                ...state,
                cart:[],
                cart_count:0,
                checkoutStatus:true                
            }
        }

        default:
            return state
    }
}

export default ProductReducer;
